<template>
    <div class="m-product-category">
        <dl class="classic">
            <dt></dt>
            <dt></dt>
            <dd v-for="(item,index) in classicList" :key="index">
                <m-select  :name="item.title" :list="item.sub_list"/>
            </dd>
        </dl>
         <dl class="classic">
            <dt></dt>
            <dt></dt>
            <dd v-for="(item,index) in areaList" :key="index">
                <m-select  :name="item.title" :list="item.subList"/>
            </dd>
        </dl>
    </div>
</template>

<script>
import MSelect from './select.vue'
export default {
  data () {
    return {
      classicList: [{
        title: '',
        type: 'food',
        sub_list: [
          {
            name: '',
            id: 'Japan'
          },
          {
            name: '',
            id: 'sichuan'
          }
        ]
      }, {
        title: '',
        type: 'hotel',
        sub_list: [
          {
            name: '',
            id: 'topic'
          },
          {
            name: '',
            id: 'eco'
          }
        ]
      }],
      areaList: [{

        title: '',

        subList: [{

          name: '',

          id: 120000

        }, {

          name: '',

          id: 12222

        }]

      }]
    }
  },
  components: {
    MSelect
  }
}
</script>

<style lang="scss" scoped>

</style>



