<template>
  <div class="map" id="map" :class="{'isFixed':isFixed}">
    <div class="myp"></div>
  </div>
</template>
<script>
import { TMap } from '@/TMap.js'
export default {
  // name:'news',
  data () {
    return {
    // api
    //   longitude: 0, // 
    //   latitude: 0, // 
    //   city: ''
      isFixed: false,
      offsetTop: 0
    }
  },
  mounted () {
    TMap('OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77').then(qq => {
      var center = new qq.maps.LatLng(39.916527, 116.397128)
      var map = new qq.maps.Map(document.getElementsByClassName('myp')[0], {
        // 
        center: center,
        zoom: 8
      })
      // marker
      var marker = new qq.maps.Marker({
        position: center,
        map: map
      })
    })

    window.addEventListener('scroll', this.initHeight)
    this.$nextTick(() => {
      // offsetTop
      this.offsetTop = document.getElementById('map').offsetTop
    })
  },
  methods: {
    initHeight () {
      // 
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // 
      this.isFixed = scrollTop > this.offsetTop ? true : false
      console.log(this.isFixed)
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.initHeight)
  }

}
</script>



